export const CONDITIONS_WARNING = (reboot = false) =>
  `One or more health check remediation conditions have been met. The node will ${
    reboot ? 'reboot' : 'reprovision'
  } automatically.`;

export const CPU_LIMIT_REQ_ERROR =
  'This nodes CPU resources are overcommitted. The total CPU resource limit of all pods exceeds the nodes total capacity. The total CPU requested is also approaching the nodes capacity. Pod performance will be throttled under high load, and new pods may not be schedulable on this node.';
export const CPU_LIMIT_ERROR =
  'This nodes CPU resources are overcommitted. The total CPU resource limit of all pods exceeds the nodes total capacity. Pod performance will be throttled under high load.';
export const CPU_LIMIT_WARN =
  'The total CPU resource limit of all pods on this node is approaching the nodes capacity. Pod performance may be throttled under high load.';
export const CPU_LIMIT_REQ_WARN =
  'The total CPU resource limit and amount requested by all pods on this node is approaching the nodes capacity. Pod performance may be throttled under high load, and new pods may not be schedulable.';
export const CPU_REQ_WARN =
  'The total CPU requested by all pods on this node is approaching the nodes capacity. New pods may not be schedulable on this node.';

export const MEM_LIMIT_REQ_ERROR =
  'This nodes memory resources are overcommitted. The total memory resource limit of all pods exceeds the nodes total capacity. The total memory requested is also approaching the nodes capacity. Pods will be terminated under high load, and new pods may not be schedulable on this node.';
export const MEM_LIMIT_ERROR =
  'This nodes memory resources are overcommitted. The total memory resource limit of all pods exceeds the nodes total capacity. Pods will be terminated under high load.';
export const MEM_LIMIT_WARN =
  'The total memory resource limit of all pods on this node is approaching the nodes capacity. Pods may be terminated if the limit is reached under high load.';
export const MEM_LIMIT_REQ_WARN =
  'The total memory resource limit and amount requested by all pods on this node is approaching the nodes capacity. Pods may be terminated if the limit is reached under high load, and new pods may not be schedulable on this node.';
export const MEM_REQ_WARN =
  'The total memory requested by all pods on this node is approaching the nodes capacity. New pods may not be schedulable on this node.';
